import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import { DropLeft, DropRight } from "@cvccorp-components/icons";
import Theme from "../../Theme";
import themes from "../../Theme/themes";
import Button from "../Button";
import { BrandType } from "../../types/brand";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        padding: "20px",
    },
}));

type NavigationButtonsProps = {
    mode: string,
    brand: BrandType,
    color: string,
    variant: string,
    size: string,
    direction: string
}


export const NavigationButtons = 
    ({
        brand,
        mode,
        color,
        variant,
        size,
        direction
    }: NavigationButtonsProps): JSX.Element => {
    const classes = useStyles(themes[brand]);

    const directions = {
        left: <DropLeft size={size} />,
        right: <DropRight size={size} />
    }

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="botoes">
                <Typography variant="h3">Botão de navegação</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={12}>
                    <Button
                        color={color}
                        size={size}
                        icon={directions[direction]}
                        nextPrev
                        variant={variant}
                        iconPosition="center"
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        color: 'primary',
        variant: 'contained',
        size: 'small',
        direction: 'left'
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"],
            brand: "CVC",
            mode: "root",
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        color: {
            options: ['default', 'primary', 'secondary'],
            control: { type: 'select' }
        },
        variant: {
            options: ['outlined', 'contained'],
            control: { type: 'radio' }
        },
        size: {
            options: ['small', 'medium', 'large'],
            control: { type: 'select' }
        },
        direction: {
            options: ['left', 'right'],
            control: { type: 'radio' }
        },
    }
}

storiesOf("Components/Buttons/Button", module)
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Navegação", (args) => <NavigationButtons {...args} />, argsControls);
